.navbar-elem {
  display: inline-block;
  padding: 0.5em; }
  .navbar-elem a {
    position: relative;
    padding: 0.25em;
    color: #ffffff;
    font-size: 1.2em;
    text-decoration: none; }
  .navbar-elem a:after {
    content: "";
    position: absolute;
    right: 0;
    width: 0;
    bottom: -3px;
    background: #ffffff;
    height: 2px;
    -webkit-transition-property: width;
    transition-property: width;
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
            transition-timing-function: ease-out; }
  .navbar-elem a:hover:after,
  .navbar-elem a:focus:after,
  .navbar-elem a:active:after {
    left: 0;
    right: auto;
    width: 100%; }
  .navbar-elem .active:after {
    left: 0;
    right: auto;
    width: 100%; }
  @media screen and (max-width: 1200px) {
    .navbar-elem a {
      font-size: 1.1em; } }
  @media screen and (max-width: 800px) {
    .navbar-elem a {
      font-size: 1em; } }
  @media screen and (max-width: 500px) {
    .navbar-elem {
      display: block; }
      .navbar-elem a:after {
        bottom: 0px; }
      .navbar-elem .active:after {
        left: 2px;
        width: 98%; } }

